.skills-title {
    margin-bottom: 20px;
}

.skill-list {
    padding: 20px;
}

.skill-item:not(:last-child) {
    margin-bottom: 15px;
}

.title-wrapper {
    display: flex;
    align-items: center;
    gap: 5px;
    margin-bottom: 8px;
}

.content-card {
    margin-bottom: 20px;
}

.skill-progress-bg {
    background: var(--jet);
    width: 100%;
    height: 8px;
    border-radius: 10px;
}

.skill-progress-fill {
    background: var(--text-gradient-yellow);
    height: 100%;
    border-radius: inherit;
    animation: skill-progress-fill 1s linear forwards;
}

.article-title-hr::after {
    height: 5px;
    width: 60px;
}
.article-title-tech::after {
    height: 5px;
    width: 100px;
}


@keyframes skill-progress-fill {
    0% {
        width: 0%;
    }
}

@media (min-width: 580px) {
    .skills-item:not(:last-child) {
        margin-bottom: 25px;
    }
}

.skills {
  padding: 20px;
}

.title-wrapper {
  display: flex;
  /* justify-content: space-between; */
}



.skill-progress-bg {
  background-color: #3d3d3d;
  border-radius: 8px;
  height: 12px;
  overflow: hidden;
}
